import React, { useState, useEffect, useRef } from 'react';
import { Map, GoogleApiWrapper, Marker } from 'google-maps-react';
import '../Destination/Destination.css';
import axios from 'axios';
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import { useAuthUser } from 'react-auth-kit'; 
import { useQuery } from '@apollo/react-hooks';
import gql from 'graphql-tag';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';





const MapContainer = ({ google }) => {
  const [selectedTripID, setSelectedTripID] = useState('');
  const [isButtonEnabled, setIsButtonEnabled] = useState(false);
  const [currentLocation, setCurrentLocation] = useState(null);
  const [searchLocation, setSearchLocation] = useState('');
  const [createdDestinationID, setCreatedDestinationID] = useState(null);
  const mapRef = useRef(null);
  const auth = useAuthUser();
  const token = localStorage.getItem('token');
  const userEmail = auth().email;
  

  const GET_DATA = gql`
    query {
      user(email: "${userEmail}") {
        trips {
          id
          name
        }
      }
    }
  `;

  const { loading, error, data } = useQuery(GET_DATA);

  useEffect(() => {
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(
        (position) => {
          const { latitude, longitude } = position.coords;
          setCurrentLocation({
            lat: latitude,
            lng: longitude,
          });
        },
        (error) => {
          console.error('Error obteniendo la ubicación:', error);
        }
      );
    } else {
      console.error('La geolocalización no es soportada por este navegador.');
    }
  },[]);

  const handleSearchLocation = () => {
    const apiKey = process.env.REACT_APP_API_KEY;
    console.log(apiKey)

    axios
      .get(`https://maps.googleapis.com/maps/api/geocode/json?address=${searchLocation}&key=${apiKey}`)
      .then((response) => {
        const results = response.data.results;

        if (results.length > 0) {
          const { lat, lng, formatted_address } = results[0].geometry.location;
          setCurrentLocation({
            lat,
            lng,
          });
          mapRef.current.map.setCenter({ lat, lng });

          let country = '';
          let city = '';

          for (const component of results[0].address_components) {
            if (component.types.includes('country')) {
              country = component.long_name;
            }
            if (component.types.includes('locality')) {
              city = component.long_name;
            }
          }

          const newDestination = {
            country: country || 'País por defecto', 
            city: city || 'Ciudad por defecto',     
            latitude: lat,
            longitude: lng,
            name: formatted_address || 'Nombre del Destino', 
          };

          createDestination(newDestination,token);
        } else {
          console.error('No se encontraron resultados para la ubicación ingresada.');
        }
      })
      .catch((error) => {
        console.error('Error al buscar la ubicación:', error);
      });
  };


  const createDestination = (destinationData, token) => {
    const headers = {
      Authorization: `Bearer ${token}`,
    };
  
    axios.post('http://127.0.0.1:3000/api/v1/destinations', destinationData, { headers })
      .then((response) => {
        console.log('Destino creado exitosamente:', response.data);
        console.log('ID', response.data.id  );
        setCreatedDestinationID(response.data.id);
        toast.success('Destino creado exitosamente');

      })
      .catch((error) => {
        console.error('Error al crear el destino:', error);
      });
  };

  const associateDestinationWithTrip = (destinationId, tripId) => {
    const headers = {
      Authorization: `Bearer ${token}`,
    };
    const associationData = {
      destination_id: destinationId,
      trip_id: tripId,
    };
  
    axios.post('http://127.0.0.1:3000/api/v1/trip_destinations', associationData, {headers})
      .then((response) => {
        console.log('Asociación creada exitosamente:', response.data);
        toast.success('Destination added to Trip');
        setIsButtonEnabled(false);
      })
      .catch((error) => {
        console.error('Error al crear la asociación:', error);
      });
  };
  



  const handleInputChange = (event) => {
    setSearchLocation(event.target.value);
  };

  if (!currentLocation) {
    return <div>Loading current loaction...</div>;
  }

  return (
    <div className="map">
      <Map google={google} zoom={14} initialCenter={currentLocation} ref={mapRef}>
        {currentLocation && <Marker position={currentLocation} />}
      </Map>

      <div className="map-search">
        <TextField
          variant="outlined"
          label="Search location..."
          value={searchLocation}
          onChange={(e) => handleInputChange(e)}
        />
        <Button variant="contained" onClick={() => handleSearchLocation()}>
          Search
        </Button>
      </div>

      <div className="trip-dropdown">
        <FormControl variant="outlined">
          <Select
          value={selectedTripID}
          onChange={(e) => {
            setSelectedTripID(e.target.value);
            setIsButtonEnabled(true);
          }}
          label="Select trip"
          >
          <MenuItem value="">
            <em>Seleccionar un viaje</em>
          </MenuItem>
          {data?.user?.trips.map((trip) => (
            <MenuItem key={trip.id} value={trip.id}>
              {trip.name}
            </MenuItem>
          ))}
          </Select>
        </FormControl>
        <Button
          variant="contained"
          disabled={!isButtonEnabled}
          onClick={() => {
            console.log(selectedTripID);
            associateDestinationWithTrip(createdDestinationID, selectedTripID);
          }}
          >
          {selectedTripID ? 'Agregar' : 'Select Trip'}
        </Button>
      </div>
    </div>
  );
};

export default GoogleApiWrapper({
  apiKey: process.env.REACT_APP_API_KEY,
})(MapContainer);
