import { useNavigate, useLocation } from 'react-router-dom';
import React, { useState } from "react";
import { Avatar, Box, Button, Container, Menu, MenuItem } from "@mui/material";
import { useSignOut } from 'react-auth-kit'
import './TopNav.css';
import image from '../../assets/default.png';

function TopNav(props) {
  const [anchorEl, setAnchorEl] = useState(null);
  const [open, setOpen] = useState(false);
  const navigate = useNavigate();
  const signOut = useSignOut();
  const location = useLocation();
  const userImageURL = "hola" || image;

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
    setOpen(true);
  };

  const handleClose = () => {
    setAnchorEl(null);
    setOpen(false);
  };

  const handleLogout = () => {
    localStorage.removeItem("token");
    signOut();
    navigate('/login');
  };

  const handleProfile = () => {
    navigate('/profile');
    setOpen(false);
  };

  // Verifica si estás en la vista Profile
  const isProfileView = location.pathname === '/profile';

  return (
    <Box className={"BoxNav"}>
      <Container sx={{ width: '100%', height: '85px', position: 'fixed', top: 0, left: 0 }}>
        <p className={"AppName"}>Travel Log</p>
        
        {/* Renderiza el botón de perfil solo si no estás en la vista Profile */}
        {!isProfileView && (
          <Button
            id="profile-button"
            aria-controls={open ? 'profile-menu' : undefined}
            aria-haspopup="true"
            aria-expanded={open ? 'true' : undefined}
            onClick={handleClick}
            sx={{ position: 'absolute', top: '16px', left: '16px' }}
          >
            <Avatar src={userImageURL} left={0} position={"absolute"} />
          </Button>
        )}

        <Menu
          id="profile-menu"
          spacing={2}
          anchorEl={anchorEl}
          open={open}
          onClose={handleClose}
          MenuListProps={{
            'aria-labelledby': 'profile-button',
          }}
        >
          <MenuItem onClick={handleProfile}>Profile</MenuItem>
          <MenuItem onClick={handleClose}>Settings</MenuItem>
          <MenuItem onClick={handleLogout}>Logout</MenuItem>
        </Menu>
      </Container>
    </Box>
  );
}

export default TopNav;
