import React, { useState } from 'react';
import { Button, Container, TextField, Typography } from '@mui/material';
import './LoginPage.css';
import { useNavigate } from 'react-router-dom';
import { useSignIn } from 'react-auth-kit';
import logo from '../../assets/travel-log.png';


function LoginPage() {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [errorMessage, setErrorMessage] = useState(null);
  const signIn = useSignIn();
  const navigate = useNavigate();

  const handleSubmit = async (e) => {
    e.preventDefault();
  
    try {
      const basicAuth = btoa(`${email}:${password}`);
  
      const response = await fetch(`${process.env.REACT_APP_BACKEND_URL}/api/v1/api-keys`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Basic ${basicAuth}`,
        },
        body: JSON.stringify({}),
      });
      if (response.ok || response.status === 201) {
        const data = await response.json();
        localStorage.setItem('token', data.token);
        localStorage.setItem('email', email);
        signIn({
            token: data.token,
            expiresIn: 3600,
            tokenType: "Bearer",
            authState: {id: data.bearer_id, email: email}
          });

          navigate('/trips');
      
      
      
        } else {
        setErrorMessage('Autenticación fallida. Verifica tus credenciales.');
      }
      


    } catch (error) {
        console.error('Error de autenticación:', error);
        setErrorMessage('Ha ocurrido un error. Inténtalo de nuevo más tarde.');
    }
  };

  return (
    <Container maxWidth="sm" className="LoginPage" >
      <div className="image-container">
        <img src={logo} alt="Logo" className="logo"/>
      </div>
      <Typography variant="h4">Bienvenido a Travel Log</Typography>
      {errorMessage && <p style={{ color: 'red' }}>{errorMessage}</p>}
      <form onSubmit={handleSubmit} className='login-form'>
        <TextField
          label="Email"
          type="email"
          value={email}
          onChange={(e) => setEmail(e.target.value)}
          variant="outlined"
          fullWidth
          required
          margin="normal"
          className='input-form'
          color='primary'
        />
        <TextField
          label="Password"
          type="password"
          value={password}
          onChange={(e) => setPassword(e.target.value)}
          variant="outlined"
          fullWidth
          required
          margin="normal"
          className='input-form'
        />
        <Button type="submit" variant="contained" className="SubmitButton">
          Iniciar sesión
        </Button>
      </form>
    </Container>
  );
}

export default LoginPage;