import React, { useState, useEffect } from 'react';
import './TripPage.css';
import Typography from '@mui/material/Typography';
import Paper from '@mui/material/Paper';
import Grid from '@mui/material/Grid';
import ImageList from '@mui/material/ImageList';
import ImageListItem from '@mui/material/ImageListItem';
import { useQuery } from '@apollo/client';
import gql from 'graphql-tag'; 
import CircularProgress from '@mui/material/CircularProgress';
import Skeleton from '@mui/material/Skeleton';
import { Map, GoogleApiWrapper, Marker } from 'google-maps-react';
import { format } from 'date-fns';

function TripPage({ google }) {
  const [mapCenter, setMapCenter] = useState({ lat: 0, lng: 0 });
  const [mapZoom, setMapZoom] = useState(4);
  const [selectedDestination, setSelectedDestination] = useState(null);
  
  useEffect(() => {
    if (selectedDestination) {
      // Cuando selectedDestination cambia, actualiza el centro y el zoom del mapa
      setMapCenter({ lat: selectedDestination.latitude, lng: selectedDestination.longitude });
      setMapZoom(5); // Ajusta el valor de zoom según tus necesidades
    }
  }, [selectedDestination]);

  const tripId  = localStorage.getItem('tripId'); 

  const GET_TRIP_DETAILS = gql`
    query {
      trip(id: "${tripId}") {
        id
        name
        description
        startDate
        endDate
        destinations {
          id
          country
          city
          latitude
          longitude
          name
        }
      }
    }
  `;

  const { loading, error, data } = useQuery(GET_TRIP_DETAILS, {
    variables: { tripId },
  });

  if (loading) return <CircularProgress />;
  if (error) return <h1>Error: {error.message}</h1>;

  const trip = data?.trip;
  const destinations = trip?.destinations || [];

  // Función para agregar los marcadores de destino en el mapa
  const renderMarkers = (dest) => {
    return dest.map((destination) => (
      <Marker
        key={destination.id}
        position={{ lat: destination.latitude, lng: destination.longitude }}
        title={destination.name}
        onClick={() => {
          setSelectedDestination(destination);
        }}
      />
    ));
  };

  return (
    <div className="TripPageContainer">
      {trip ? (
        <>
          <Typography variant="h4">{trip.name}</Typography>
          <Typography variant="body1">{trip.description}</Typography>
          <Typography variant="body1">Inicio: {format(new Date(trip.startDate), 'dd/MM/yyyy')}</Typography>
          <Typography variant="body1">Fin: {format(new Date(trip.endDate), 'dd/MM/yyyy')}</Typography>

          {trip.images && trip.images.length > 0 ? (
            <Grid container spacing={2}>
              <Grid item xs={12}>
                <ImageList cols={2} gap={8}>
                  {trip.images.map((imageUrl, index) => (
                    <ImageListItem key={index}>
                      {loading ? (
                        <Skeleton variant="rectangular" width={200} height={200} />
                      ) : (
                        <img src={imageUrl} alt={`Imagen ${index + 1}`} />
                      )}
                    </ImageListItem>
                  ))}
                </ImageList>
              </Grid>
            </Grid>
          ) : (
            <Paper elevation={3} className="NoImagesPaper">
              <Typography variant="body1">For this Trip there are no images</Typography>
            </Paper>
          )}
          <div className='google-map'>
          <Map
            google={google}
            zoom={mapZoom}
            initialCenter={mapCenter}
          >
            {renderMarkers(destinations)}
          </Map>
          {selectedDestination && (
            <div className="DestinationDetails">
              <Typography variant="h6">Destination Details</Typography>
              <Typography variant="body1">Name: {selectedDestination.name}</Typography>
              <Typography variant="body1">Country: {selectedDestination.country}</Typography>
              <Typography variant="body1">City: {selectedDestination.city}</Typography>
              <button onClick={() => setSelectedDestination(null)}>Close</button>
            </div>
          )}
          </div>
        </>
      ) : (
        <Typography variant="body1">No details</Typography>
      )}
    </div>
  );
}
// Aquí envolvemos el componente TripPage con GoogleApiWrapper
export default GoogleApiWrapper({
  apiKey: process.env.REACT_APP_API_KEY,
})(TripPage);
