import React, { useState, useRef, useEffect } from "react";
import { useAuthUser } from 'react-auth-kit'; 
import CircularProgress from "@mui/material/CircularProgress";
import { useQuery } from '@apollo/react-hooks';
import gql from 'graphql-tag';
import './Profile.css';
import axios from "axios";
import defaultImage from '../../assets/default.png';

function Profile() {
  const [avatar, setAvatar] = useState(null);
  const [selectedImage, setSelectedImage] = useState(null);
  const auth = useAuthUser();
  const userEmail = auth().email;
  const id = auth().id;
  const token = localStorage.getItem('token');
  console.log(id);
  const GET_DATA = gql`
    query {
      user(email: "${userEmail}") {
        id
        firstName
        lastName
      }
    }
  `;
  const { loading, error, data } = useQuery(GET_DATA);

  const inputRef = useRef(null);

  const handleAvatarChange = (event) => {
    const selectedAvatar = event.target.files[0];
    setSelectedImage(selectedAvatar);
  };

  
  const uploadImage = async () => {
    const formData = new FormData();
    formData.append("avatar", selectedImage);
  
    try {
      await axios.post(`${process.env.REACT_APP_BACKEND_URL}/api/v1/users/${id}/update_avatar`, formData, {
        headers: {
          "Content-Type": "multipart/form-data",
          Authorization: `Bearer ${token}`, 
        },
      });
      console.log("IMAGEN SUBIDAAA");
  
    } catch (error) {
      console.error("Error al subir la imagen:", error);
    }
  };

  // Función para obtener la imagen del usuario desde Active Storage
  const fetchAvatar = async () => {
    try {
      const response = await axios.get(`${process.env.REACT_APP_API_URL}/api/v1/users/${id}/avatar`, {
        headers: {
          Authorization: `Bearer ${token}`,
        },
        responseType: 'blob',
      });
      console.log("IMAGEN FETCHEADA");

      const imageUrl = URL.createObjectURL(response.data);
      setAvatar(imageUrl);
    } catch (error) {
      console.error("Error al obtener la imagen:", error);
    }
  };

  useEffect(() => {
    fetchAvatar(); 
  }, [id]);

  if (loading) return <CircularProgress />;
  if (error) return <h1>Error: {error.message}</h1>;
  const userData = data.user;



  return (
    <div>
      <div className="avatar-container">
        <img
          src={selectedImage ? URL.createObjectURL(selectedImage) : avatar}
          alt="Avatar"
          className="avatar-image" 
        />
      </div>
      <input
        type="file"
        accept="image/*"
        onChange={handleAvatarChange}
        ref={inputRef}
        style={{ display: 'none' }}
      />
      <button onClick={() => inputRef.current.click()}>Seleccionar Imagen</button>
      <button onClick={uploadImage}>Subir Foto</button>
      <h1>Perfil de Usuario</h1>
      <p>Nombre: {userData.firstName} {userData.lastName}</p>
    </div>
  );
}

export default Profile;
